<template>
  <div id="app">
    <div class="app-container">
      <!-- autocomplete -->
      <app-autocomplete class="elevated" :world-countries="worldCountries" @select="onSelect"></app-autocomplete>
      <!-- results -->
      <template v-if="userData.length > 0">
        <div v-for="(stats, index) of userData" :key="index">
          <app-statistics
            class="elevated"
            :statistics="stats"
            :favourite="userBookmarks.includes(stats.country.toLowerCase())"
            @pin="onPin"
            @unPin="onUnpin"
            @delete="onDelete"
          ></app-statistics>
        </div>
      </template>
      <template v-else>
        <div class="no-data">
          <div>No Countries Selected</div>
          <div>Add Some</div>
        </div>
      </template>
      <!-- toast -->
      <!-- <div :class="t_active ? [...t_base, 'active'] : [...t_base]">{{t_message}}</div> -->
      <div :class="t_active ? ['toast', 'active'] : ['toast']">{{t_message}}</div>
      <!-- toast -->
    </div>
  </div>
</template>

<script>
import axios from "axios";
import AppAutocomplete from "./components/AppAutocomplete";
import AppStatistics from "./components/AppStatistics";

export default {
  name: "App",

  components: {
    AppAutocomplete,
    AppStatistics
  },

  data() {
    return {
      worldCountries: [],
      userCountries: new Set(),
      userBookmarks: [],
      userData: [],
      // toast state *
      t_base: ["toast"],
      t_active: false,
      t_message: ""
    };
  },

  methods: {
    onSelect(country) {
      this.userCountries = new Set(
        this.userCountries.add(country.toLowerCase())
      );
      const queryData = JSON.stringify(Array.from(this.userCountries));
      const query = `
      query statsQuery {
          stats(countries: ${queryData}) {
            country
            cases
            deaths
            active
            critical
            recovered
            todayCases
            todayDeaths
            casesPerOneMillion
          }
        } 
      `;
      axios({
        url: "/graphql",
        method: "post",
        data: { query: query }
      }).then(result => {
        const { data } = result.data;
        this.userData = data.stats;
      });
    },

    onDelete(country) {
      if (this.userCountries.delete(country.toLowerCase())) {
        this.userCountries = new Set(this.userCountries);
        this.userData = this.userData.filter(item => {
          return item.country.toLowerCase() != country.toLowerCase();
        });
      }
    },

    onPin(country) {
      let newCountry = country.toLowerCase();
      let bookmarks = JSON.parse(localStorage.getItem("bookmarks"));
      if (!bookmarks) {
        this.userBookmarks.push(newCountry);
      } else if (!this.userBookmarks.includes(newCountry)) {
        this.userBookmarks = [...bookmarks, newCountry];
      }
      localStorage.setItem("bookmarks", JSON.stringify(this.userBookmarks));
      this.onNotify(`${country} added to Favourites`);
    },

    onUnpin(country) {
      this.userBookmarks = this.userBookmarks.filter(item => {
        return item !== country.toLowerCase();
      });
      localStorage.setItem("bookmarks", JSON.stringify(this.userBookmarks));
      this.onNotify(`${country} removed from Favourites`);
    },

    // toast method
    onNotify(message) {
      const vm = this;
      if (!vm.t_active) {
        vm.t_active = true;
        vm.t_message = message;
        console.log("should open");
        setTimeout(function() {
          console.log("should close");
          vm.t_active = false;
        }, 1800);
      }
    }
  },

  created() {
    const query = `
      query countryQuery {
        countries {
          country
        }
      }`;
    axios({
      url: "/graphql",
      method: "post",
      data: { query: query }
    }).then(result => {
      const { data } = result.data;
      this.worldCountries = data.countries.map(item => {
        return item.country.toLowerCase();
      });
    });
  },

  mounted() {
    const bookmarks = localStorage.getItem("bookmarks");
    if (bookmarks) {
      this.userCountries = new Set(JSON.parse(bookmarks));
      this.userBookmarks = JSON.parse(bookmarks);
      const query = `
        query statsQuery {
            stats(countries: ${bookmarks}) {
              country
              cases
              deaths
              active
              critical
              recovered
              todayCases
              todayDeaths
              casesPerOneMillion
            }
          }
        `;
      axios({
        url: "/graphql",
        method: "post",
        data: { query: query }
      }).then(result => {
        const { data } = result.data;
        this.userData = data.stats;
      });
    }
  }
};
</script>

<style lang="scss">
*,
*:before,
*:after {
  line-height: 1.5;
  box-sizing: border-box;
  font-family: "Montserrat", "Helvetica Neue", sans-serif;
}

body {
  margin: 0;
  padding: 0;
  font-size: 14px;
  color: #000;
  background: lighten(#000, 95);
}

.elevated {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
    0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
}

.no-data {
  text-align: center;
  margin-top: 3rem;
  font-size: 1.25rem;
  font-weight: 400;
  color: rgb(173, 14, 93);
}

#app {
  .app-container {
    min-width: 320px;
    max-width: 425px;
    height: 100vh;
    margin: 0 auto;
    padding: 0.5rem;
  }
}

// toast style
.toast {
  text-transform: capitalize;
  display: inline-block;
  font-size: 10px;
  color: #fff;
  position: absolute;
  left: 50%;
  bottom: 100%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.6);
  border-radius: 4px;
  padding: 0.5rem 1.5rem;
  opacity: 10%;

  @keyframes openToast {
    0% {
      opacity: 0;
      bottom: 0;
    }
    10% {
      opacity: 100%;
      bottom: calc(0px + 1rem);
    }
    90% {
      opacity: 100%;
    }
    100% {
      opacity: 0%;
      bottom: calc(0px + 1rem);
    }
  }

  &.active {
    animation: openToast 1.8s ease-in forwards;
  }
}
</style>
